.phone, .mail, .audit {
  background: url("sprite.svg") no-repeat;
}

.audit {
  background-position: 0 0;
}

.audit-dims {
  width: 73px;
  height: 64px;
}

.mail {
  background-position: 73.7373737374% 71.1111111111%;
}

.mail-dims {
  width: 26px;
  height: 26px;
}

.phone {
  background-position: 100% 100%;
}

.phone-dims {
  width: 26px;
  height: 26px;
}

section.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}

section.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.logo h1 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 6.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  margin-bottom: 72px;
  color: #c61132;
}

.title h2 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 4.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  color: #ffbb00;
}

.content h4 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 1.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  color: #093600;
}

section.footer {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}

@font-face {
  font-family: beer money;
  src: url("/fonts/beer money.woff2") format("woff2");
}
body {
  font-family: "Matemasie";
  background: #a9ee8d;
  color: #c61132;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}